.navigation--mobile {
  order: -1;

  @media (min-width: @desktop-width) {
    order: 0;
  }
}

.navigation__button-login {
  display: flex;
  align-items: center;
}

@media (max-width: 1239px) {
  .navigation--closed .navigation__list {
    // display: none;
    transition: 0.5s;
    transform: translateX(-100%);
  }
}

@media (max-width: 1239px) {
  .navigation--open .navigation__list {
    transition: 0.5s;
  }
}

// Nav

.navigation__list {
  min-height: 100%;
  z-index: 2;
  position: absolute;
  top: 47px;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: @light-green;
  padding: 20px;
  // color: @white;

  @media (min-width: @tablet-width) {
    top: 57px;
  }

  @media (min-width: @desktop-width) {
    position: static;
    display: flex;
    justify-content: center;
    color: @basic-black;
    background-color: @white;
    padding: 0;
  }
}

.navigation__mobile-login {
  @media (min-width: @desktop-width) {
    display: none;
  }
}

.navigation__login-text {
  font-size: 15px;
}

.navigation__item {
  &:not(:last-child) {
    margin-bottom: 20px;
  }
  @media (min-width: @desktop-width) {
    &:not(:last-child) {
      margin-bottom: 0;
    }
  }
}

.navigation__item-link {
  display: block;
  font-size: 18px;
  font-weight: 700;
  color: inherit;
  margin-bottom: 15px;

  @media (min-width: @desktop-width) {
    font-size: inherit;
    line-height: 20px;
    font-weight: 400;
    margin: 0;
    padding: 18px 10px 20px 10px;
    position: relative;

    &:hover::after {
      content: '';
      position: absolute;
      right: 0;
      bottom: 0;
      left: 0;
      height: 2px;
      background: @basic-green;
    }
  }
}

.navigation__item-link--closed ~ .navigation__submenu {
  display: none;
}

@media (min-width: @desktop-width) {
  .navigation__submenu {
    display: none;
    position: absolute;
  }

  .navigation__item:hover .navigation__submenu,
  .navigation__item:focus-within .navigation__submenu {
    display: block;
    width: 1200px;
    height: 500px;
    margin-top: 1px;
    left: 50%;
    transform: translateX(-50%);
    z-index: 1;
    background-color: @white;
    // border-bottom: 1px solid @basic-green;
    box-shadow: 0px 5px 10px 0px rgba(0, 0, 0, 0.1);
    padding: 40px 100px 40px 145px;
    box-sizing: border-box;

    &--sale {
      display: grid;
      grid-template-columns: 1fr 1fr;
      column-gap: 50px;
    }
  }
}

.navigation__submenu-title {
  display: block;
  font-size: 16px;
  font-weight: 700;
  color: inherit;
  margin-bottom: 10px;

  @media (min-width: @desktop-width) {
    margin-bottom: 20px;
  }
}

.navigation__submenu-list {
  &:not(:last-child) {
    margin-bottom: 15px;
  }

  @media (min-width: @desktop-width) {
    display: grid;
    grid-template-columns: 200px max-content;
    column-gap: 40px;
    row-gap: 10px;

    &:not(:last-child) {
      margin-bottom: 30px;
    }
  }
}

.navigation__submenu-item {
  &:not(:last-child) {
    margin-bottom: 5px;
  }
}

.navigation__submenu-link {
  font-size: 14px;
  color: inherit;

  @media (min-width: @desktop-width) {
    font-size: inherit;
  }
}
